import React, { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { Button, Typography, List, ListItem, ListItemText, ListItemSecondaryAction, IconButton, Grid, Dialog, DialogTitle, DialogContent, DialogActions, Table, TableBody, TableRow, TableCell, TextField, Box, Select, MenuItem, FormControl, InputLabel } from '@mui/material';
import { Delete } from '@mui/icons-material';
import { io } from 'socket.io-client';
import { addToCart, removeFromCart, clearCart, setCart } from '../../store/cartSlice';
import './PosInterface.css';

// Initialize WebSocket connection
const socket = io('http://localhost:5000', { autoConnect: true });

// Check if running in Electron
const isElectron = navigator.userAgent.toLowerCase().includes('electron');

const PosInterface = () => {
  console.log('Rendering PosInterface (Product Tiles + WebSocket)');
  const dispatch = useDispatch();
  const cartItems = useSelector((state) => state.cart.items);
  const [openReceipt, setOpenReceipt] = useState(false);
  const [receiptData, setReceiptData] = useState({ items: [], total: 0 });
  const [discountType, setDiscountType] = useState('percentage'); // 'percentage' or 'amount'
  const [discountValue, setDiscountValue] = useState(0); // Discount percentage or amount
  const [discountInput, setDiscountInput] = useState('');

  const products = [
    { id: 'coffee', name: 'Coffee', price: 5 },
    { id: 'bagel', name: 'Bagel', price: 3 },
    { id: 'sandwich', name: 'Sandwich', price: 8 },
  ];

  const layout = [
    { id: 1, type: 'button', text: 'Checkout', left: 10, top: 10 },
    { id: 2, type: 'button', text: 'Clear Cart', left: 10, top: 50 },
  ];

  // Handle WebSocket events
  useEffect(() => {
    socket.on('connect', () => {
      console.log('Connected to WebSocket server');
    });

    socket.on('cartUpdate', (cart) => {
      console.log('Received cart update:', cart);
      dispatch(setCart(cart));
    });

    socket.on('disconnect', () => {
      console.log('Disconnected from WebSocket server');
    });

    return () => {
      socket.off('cartUpdate');
      socket.off('connect');
      socket.off('disconnect');
    };
  }, [dispatch]);

  const handleButtonClick = (text) => {
    console.log(`Button clicked: ${text}`);
    console.log('Current cartItems:', cartItems);
    if (text === 'Clear Cart') {
      console.log('Clearing cart');
      dispatch(clearCart());
      socket.emit('clearCart');
      setDiscountValue(0);
      setDiscountInput('');
    } else if (text === 'Checkout') {
      if (cartItems.length === 0) {
        console.log('Cart is empty, cannot checkout');
        return;
      }
      console.log('Initiating checkout');
      const subtotal = cartItems.reduce((sum, item) => sum + item.price * item.quantity, 0);
      const discountAmount = discountType === 'percentage'
        ? subtotal * (discountValue / 100)
        : discountValue;
      const total = Math.max(0, subtotal - discountAmount); // Ensure total doesn't go negative
      const transaction = {
        items: [...cartItems],
        total,
        discount: discountAmount,
        discountType,
        discountValue,
      };
      setReceiptData(transaction);
      console.log('Emitting checkout event with transaction:', transaction);
      socket.emit('checkout', transaction);
      console.log('Setting openReceipt to true');
      setOpenReceipt(true);
    }
  };

  const handleProductClick = (product) => {
    console.log('Adding product to cart:', product);
    dispatch(addToCart(product));
    socket.emit('addToCart', product);
  };

  const handleRemoveItem = (item) => {
    console.log('Removing item from cart:', item);
    dispatch(removeFromCart({ id: item.id }));
    socket.emit('removeFromCart', { id: item.id });
  };

  const handleApplyDiscount = () => {
    const value = parseFloat(discountInput);
    if (isNaN(value) || value < 0) {
      alert('Please enter a valid discount value (0 or greater)');
      return;
    }
    if (discountType === 'percentage' && value > 100) {
      alert('Percentage discount cannot exceed 100%');
      return;
    }
    setDiscountValue(value);
  };

  const handlePrintReceipt = async () => {
    const printContent = `
      <html>
        <head>
          <title>Receipt</title>
          <style>
            body { font-family: Arial, sans-serif; padding: 20px; }
            table { width: 100%; border-collapse: collapse; }
            th, td { border: 1px solid #000; padding: 5px; text-align: left; }
            th { background-color: #f0f0f0; }
            .total { font-weight: bold; }
          </style>
        </head>
        <body>
          <h2>Receipt</h2>
          <table>
            <thead>
              <tr>
                <th>Item</th>
                <th>Price</th>
                <th>Quantity</th>
                <th>Subtotal</th>
              </tr>
            </thead>
            <tbody>
              ${receiptData.items.map(item => `
                <tr>
                  <td>${item.name}</td>
                  <td>$${item.price.toFixed(2)}</td>
                  <td>${item.quantity}</td>
                  <td>$${(item.price * item.quantity).toFixed(2)}</td>
                </tr>
              `).join('')}
              ${receiptData.discount > 0 ? `
                <tr>
                  <td colspan="3">Subtotal</td>
                  <td>$${(receiptData.total + receiptData.discount).toFixed(2)}</td>
                </tr>
                <tr>
                  <td colspan="3">Discount (${receiptData.discountType === 'percentage' ? `${receiptData.discountValue}%` : `$${receiptData.discountValue}`})</td>
                  <td>-$${receiptData.discount.toFixed(2)}</td>
                </tr>
              ` : ''}
              <tr class="total">
                <td colspan="3">Total</td>
                <td>$${receiptData.total.toFixed(2)}</td>
              </tr>
              <tr>
                <td colspan="4">Date: ${new Date().toLocaleString()}</td>
              </tr>
            </tbody>
          </table>
        </body>
      </html>
    `;

    if (isElectron) {
      // Running in Electron, use IPC to print
      try {
        const { ipcRenderer } = window.require('electron');
        await ipcRenderer.invoke('printReceipt', printContent);
        console.log('Print request sent via IPC');
      } catch (error) {
        console.error('Failed to print via IPC:', error);
        alert('Failed to print receipt. Please try again.');
      }
    } else {
      // Running in web app, use window.open
      const printWindow = window.open('', '_blank');
      printWindow.document.write(printContent);
      printWindow.document.close();
      printWindow.onload = () => {
        printWindow.print();
        printWindow.close();
      };
    }
  };

  const subtotal = cartItems.reduce((total, item) => total + item.price * item.quantity, 0);
  const discountAmount = discountType === 'percentage'
    ? subtotal * (discountValue / 100)
    : discountValue;
  const total = Math.max(0, subtotal - discountAmount);

  return (
    <div style={{ display: 'flex', padding: '20px' }}>
      <div style={{ width: '50%', marginRight: '20px' }}>
        <Typography variant="h6" gutterBottom>Products</Typography>
        <Grid container spacing={2}>
          {products.map((product) => (
            <Grid item xs={4} key={product.id}>
              <Button
                variant="contained"
                color="secondary"
                fullWidth
                onClick={() => handleProductClick(product)}
              >
                {product.name} (${product.price})
              </Button>
            </Grid>
          ))}
        </Grid>
        <div
          style={{
            marginTop: '20px',
            height: '200px',
            position: 'relative',
            background: '#f0f0f0',
            border: '1px solid #ccc',
            padding: '10px',
          }}
        >
          {layout.map((item) => (
            <Button
              key={item.id}
              variant="contained"
              color="primary"
              style={{ position: 'absolute', left: item.left, top: item.top }}
              onClick={() => handleButtonClick(item.text)}
              disabled={item.text === 'Checkout' && cartItems.length === 0}
            >
              {item.text}
            </Button>
          ))}
          <p style={{ position: 'absolute', bottom: '10px', left: '10px', color: '#666' }}>
            Note: Drag-and-drop is disabled temporarily.
          </p>
        </div>
      </div>
      <div style={{ width: '50%' }}>
        <Typography variant="h6">Cart Contents</Typography>
        {cartItems.length === 0 ? (
          <Typography>Cart is empty</Typography>
        ) : (
          <>
            <List>
              {cartItems.map((item) => (
                <ListItem key={item.id}>
                  <ListItemText
                    primary={`${item.name} - $${item.price} x ${item.quantity}`}
                    secondary={`Subtotal: $${(item.price * item.quantity).toFixed(2)}`}
                  />
                  <ListItemSecondaryAction>
                    <IconButton onClick={() => handleRemoveItem(item)}>
                      <Delete />
                    </IconButton>
                  </ListItemSecondaryAction>
                </ListItem>
              ))}
            </List>
            <Box sx={{ mt: 2, display: 'flex', alignItems: 'center' }}>
              <FormControl size="small" sx={{ width: '150px', mr: 1 }}>
                <InputLabel>Discount Type</InputLabel>
                <Select
                  value={discountType}
                  onChange={(e) => {
                    setDiscountType(e.target.value);
                    setDiscountValue(0);
                    setDiscountInput('');
                  }}
                  label="Discount Type"
                >
                  <MenuItem value="percentage">Percentage (%)</MenuItem>
                  <MenuItem value="amount">Amount ($)</MenuItem>
                </Select>
              </FormControl>
              <TextField
                label={discountType === 'percentage' ? 'Discount (%)' : 'Discount ($)'}
                value={discountInput}
                onChange={(e) => setDiscountInput(e.target.value)}
                type="number"
                size="small"
                sx={{ width: '100px', mr: 1 }}
              />
              <Button
                variant="contained"
                onClick={handleApplyDiscount}
                size="small"
              >
                Apply Discount
              </Button>
            </Box>
            {discountValue > 0 && (
              <Typography variant="subtitle1" sx={{ mt: 1 }}>
                Subtotal: ${subtotal.toFixed(2)}<br />
                Discount ({discountType === 'percentage' ? `${discountValue}%` : `$${discountValue}`}): -${discountAmount.toFixed(2)}
              </Typography>
            )}
            <Typography variant="subtitle1" sx={{ mt: 1 }}>
              Total: ${total.toFixed(2)}
            </Typography>
          </>
        )}
      </div>

      {/* Receipt Modal */}
      <Dialog open={openReceipt} onClose={() => setOpenReceipt(false)}>
        <DialogTitle>Receipt</DialogTitle>
        <DialogContent>
          <Table>
            <TableBody>
              {receiptData.items.map((item) => (
                <TableRow key={item.id}>
                  <TableCell>{item.name}</TableCell>
                  <TableCell align="right">${item.price.toFixed(2)}</TableCell>
                  <TableCell align="right">{item.quantity}</TableCell>
                  <TableCell align="right">${(item.price * item.quantity).toFixed(2)}</TableCell>
                </TableRow>
              ))}
              {receiptData.discount > 0 && (
                <>
                  <TableRow>
                    <TableCell colSpan={3}>Subtotal</TableCell>
                    <TableCell align="right">${(receiptData.total + receiptData.discount).toFixed(2)}</TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell colSpan={3}>Discount ({receiptData.discountType === 'percentage' ? `${receiptData.discountValue}%` : `$${receiptData.discountValue}`})</TableCell>
                    <TableCell align="right">-${receiptData.discount.toFixed(2)}</TableCell>
                  </TableRow>
                </>
              )}
              <TableRow>
                <TableCell colSpan={3}><strong>Total</strong></TableCell>
                <TableCell align="right"><strong>${receiptData.total.toFixed(2)}</strong></TableCell>
              </TableRow>
              <TableRow>
                <TableCell colSpan={4}>Date: {new Date().toLocaleString()}</TableCell>
              </TableRow>
            </TableBody>
          </Table>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setOpenReceipt(false)}>Close</Button>
          <Button onClick={handlePrintReceipt} variant="contained" color="primary">Print Receipt</Button>
        </DialogActions>
      </Dialog>
    </div>
  );
};

export default PosInterface;