import { createSlice } from '@reduxjs/toolkit';

const dashboardSlice = createSlice({
  name: 'dashboard',
  initialState: {
    widgets: [
      { id: 1, type: 'sales', x: 0, y: 0, width: 4, height: 2 },
      { id: 2, type: 'inventory', x: 4, y: 0, width: 4, height: 2 },
    ],
  },
  reducers: {
    updateWidgets: (state, action) => {
      state.widgets = action.payload;
    },
  },
});

export const { updateWidgets } = dashboardSlice.actions;
export default dashboardSlice.reducer;