import React, { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { Typography, Box, Grid, Paper, Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from '@mui/material';
import { io } from 'socket.io-client';
import { setCart } from '../../store/cartSlice';

// Initialize WebSocket connection
const socket = io('http://localhost:5000', { autoConnect: true });

const Dashboard = () => {
  console.log('Rendering Dashboard');
  const dispatch = useDispatch();
  const cartItems = useSelector((state) => state.cart.items);
  const [transactions, setTransactions] = useState([]);

  // Calculate sales metrics from transactions
  const salesMetrics = transactions.reduce(
    (metrics, tx) => ({
      totalSales: metrics.totalSales + tx.total,
      itemsSold: metrics.itemsSold + tx.items.reduce((sum, item) => sum + item.quantity, 0),
      transactions: metrics.transactions + 1,
    }),
    { totalSales: 0, itemsSold: 0, transactions: 0 }
  );

  // Handle WebSocket events
  useEffect(() => {
    socket.on('connect', () => {
      console.log('Dashboard connected to WebSocket server');
    });

    socket.on('cartUpdate', (cart) => {
      console.log('Dashboard received cart update:', cart);
      dispatch(setCart(cart));
    });

    socket.on('transactionsUpdate', (updatedTransactions) => {
      console.log('Dashboard received transactions update:', updatedTransactions);
      setTransactions(updatedTransactions);
    });

    socket.on('disconnect', () => {
      console.log('Dashboard disconnected from WebSocket server');
    });

    return () => {
      socket.off('cartUpdate');
      socket.off('transactionsUpdate');
      socket.off('connect');
      socket.off('disconnect');
    };
  }, [dispatch]);

  const cartTotal = cartItems.reduce((total, item) => total + item.price * item.quantity, 0);

  return (
    <Box sx={{ padding: 3 }}>
      <Typography variant="h4" gutterBottom>
        POS Dashboard
      </Typography>
      <Grid container spacing={3}>
        {/* Sales Metrics */}
        <Grid item xs={12} md={4}>
          <Paper sx={{ p: 2 }}>
            <Typography variant="h6">Total Sales</Typography>
            <Typography variant="h4">${salesMetrics.totalSales.toFixed(2)}</Typography>
          </Paper>
        </Grid>
        <Grid item xs={12} md={4}>
          <Paper sx={{ p: 2 }}>
            <Typography variant="h6">Items Sold</Typography>
            <Typography variant="h4">{salesMetrics.itemsSold}</Typography>
          </Paper>
        </Grid>
        <Grid item xs={12} md={4}>
          <Paper sx={{ p: 2 }}>
            <Typography variant="h6">Transactions</Typography>
            <Typography variant="h4">{salesMetrics.transactions}</Typography>
          </Paper>
        </Grid>
        {/* Current Cart */}
        <Grid item xs={12}>
          <Paper sx={{ p: 2 }}>
            <Typography variant="h6" gutterBottom>
              Current Cart
            </Typography>
            {cartItems.length === 0 ? (
              <Typography>Cart is empty</Typography>
            ) : (
              <>
                <TableContainer>
                  <Table>
                    <TableHead>
                      <TableRow>
                        <TableCell>Item</TableCell>
                        <TableCell align="right">Price</TableCell>
                        <TableCell align="right">Quantity</TableCell>
                        <TableCell align="right">Subtotal</TableCell>
                      </TableRow>
                    </TableHead>
                    <TableBody>
                      {cartItems.map((item) => (
                        <TableRow key={item.id}>
                          <TableCell>{item.name}</TableCell>
                          <TableCell align="right">${item.price.toFixed(2)}</TableCell>
                          <TableCell align="right">{item.quantity}</TableCell>
                          <TableCell align="right">${(item.price * item.quantity).toFixed(2)}</TableCell>
                        </TableRow>
                      ))}
                    </TableBody>
                  </Table>
                </TableContainer>
                <Typography variant="subtitle1" sx={{ mt: 2 }}>
                  Total: ${cartTotal.toFixed(2)}
                </Typography>
              </>
            )}
          </Paper>
        </Grid>
        {/* Recent Transactions */}
        <Grid item xs={12}>
          <Paper sx={{ p: 2 }}>
            <Typography variant="h6" gutterBottom>
              Recent Transactions
            </Typography>
            {transactions.length === 0 ? (
              <Typography>No transactions yet</Typography>
            ) : (
              <TableContainer>
                <Table>
                  <TableHead>
                    <TableRow>
                      <TableCell>ID</TableCell>
                      <TableCell>Items</TableCell>
                      <TableCell align="right">Total</TableCell>
                      <TableCell align="right">Discount</TableCell>
                      <TableCell>Timestamp</TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {transactions.map((tx) => (
                      <TableRow key={tx.id}>
                        <TableCell>{tx.id}</TableCell>
                        <TableCell>{tx.items.map(item => `${item.name} x${item.quantity}`).join(', ')}</TableCell>
                        <TableCell align="right">${tx.total.toFixed(2)}</TableCell>
                        <TableCell align="right">
                          ${(tx.discount || 0).toFixed(2)} 
                          {tx.discount > 0 && ` (${tx.discountType === 'percentage' ? `${tx.discountValue}%` : `$${tx.discountValue}`})`}
                        </TableCell>
                        <TableCell>{new Date(tx.timestamp).toLocaleString()}</TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </TableContainer>
            )}
          </Paper>
        </Grid>
      </Grid>
    </Box>
  );
};

export default Dashboard;