import React, { useEffect, useRef, useState } from 'react';
import JsBarcode from 'jsbarcode';
import { TextField, Button, Select, MenuItem, FormControl, InputLabel, Box, Typography } from '@mui/material';

const BarcodeGenerator = () => {
  const canvasRef = useRef(null);
  const [code, setCode] = useState('123456789');
  const [format, setFormat] = useState('CODE128');
  const [error, setError] = useState(null);
  const [barcodeHistory, setBarcodeHistory] = useState([]);

  const formats = ['CODE128', 'UPC', 'EAN13', 'EAN8', 'CODE39'];

  useEffect(() => {
    if (canvasRef.current && code) {
      try {
        JsBarcode(canvasRef.current, code, {
          format,
          width: 2,
          height: 100,
          displayValue: true,
        });
        setError(null);
      } catch (err) {
        setError('Invalid barcode value for selected format');
      }
    }
  }, [code, format]);

  const addToHistory = (image) => {
    setBarcodeHistory((prev) => [
      { code, format, image },
      ...prev.slice(0, 4), // Keep only the last 5 entries
    ]);
  };

  const saveBarcode = () => {
    if (!canvasRef.current) return;
    const image = canvasRef.current.toDataURL('image/png');
    const link = document.createElement('a');
    link.href = image;
    link.download = `barcode-${code}-${format}.png`;
    link.click();
    addToHistory(image);
  };

  const printBarcode = async () => {
    if (!canvasRef.current) return;

    const image = canvasRef.current.toDataURL('image/png');
    const printContent = `
      <html>
        <body onload="window.print(); window.close();">
          <img src="${image}" />
        </body>
      </html>
    `;
    if (window.electronAPI) {
      try {
        const result = await window.electronAPI.printReceipt(printContent);
        if (result.success) {
          alert(`Barcode PDF saved at: ${result.filePath}`);
          // Don't add to history since desktop printing is failing
        }
      } catch (err) {
        console.error('Printing failed:', err);
        alert('Failed to generate barcode PDF');
      }
    } else {
      const printWindow = window.open('', '_blank');
      printWindow.document.write(printContent);
      printWindow.document.close();
      addToHistory(image); // Add to history only for web app
    }
  };

  return (
    <Box sx={{ padding: '20px' }}>
      <Typography variant="h6" gutterBottom>Barcode Generator</Typography>
      <FormControl fullWidth margin="normal">
        <InputLabel>Barcode Format</InputLabel>
        <Select
          value={format}
          onChange={(e) => setFormat(e.target.value)}
          label="Barcode Format"
        >
          {formats.map((fmt) => (
            <MenuItem key={fmt} value={fmt}>{fmt}</MenuItem>
          ))}
        </Select>
      </FormControl>
      <TextField
        label="Barcode Value"
        value={code}
        onChange={(e) => setCode(e.target.value)}
        error={!!error}
        helperText={error}
        fullWidth
        margin="normal"
      />
      <canvas ref={canvasRef} />
      <Box sx={{ mt: 2 }}>
        <Button
          variant="contained"
          onClick={saveBarcode}
          disabled={!code || !!error}
          sx={{ mr: 1 }}
        >
          Save as PNG
        </Button>
        <Button
          variant="contained"
          onClick={printBarcode}
          disabled={!code || !!error}
        >
          Print Barcode
        </Button>
      </Box>
      {barcodeHistory.length > 0 && (
        <Box sx={{ mt: 4 }}>
          <Typography variant="h6" gutterBottom>Barcode History</Typography>
          {barcodeHistory.map((entry, index) => (
            <Box key={index} sx={{ mb: 2 }}>
              <Typography>{entry.code} ({entry.format})</Typography>
              <img src={entry.image} alt={`Barcode ${entry.code}`} style={{ maxWidth: '200px' }} />
            </Box>
          ))}
        </Box>
      )}
    </Box>
  );
};

export default BarcodeGenerator;