import React from 'react';
import { Provider } from 'react-redux';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import { store } from './store/index';
import ErrorBoundary from './components/ErrorBoundary';
import PosInterface from './components/PosInterface/PosInterface';
import Dashboard from './components/Dashboard/Dashboard';
import BarcodeGenerator from './components/BarcodeGenerator/BarcodeGenerator';
import LabelDesigner from './components/LabelDesigner/LabelDesigner';
import AppBuilder from './components/AppBuilder/AppBuilder';
import TestDnd from './components/TestDnd';

function App() {
  return (
    <Provider store={store}>
      <Router>
        <Routes>
          <Route
            path="/"
            element={
              <ErrorBoundary>
                <PosInterface />
              </ErrorBoundary>
            }
          />
          <Route
            path="/dashboard"
            element={
              <ErrorBoundary>
                <Dashboard />
              </ErrorBoundary>
            }
          />
          <Route
            path="/barcode"
            element={
              <ErrorBoundary>
                <BarcodeGenerator />
              </ErrorBoundary>
            }
          />
          <Route
            path="/label-designer"
            element={
              <ErrorBoundary>
                <LabelDesigner />
              </ErrorBoundary>
            }
          />
          <Route
            path="/app-builder"
            element={
              <ErrorBoundary>
                <AppBuilder />
              </ErrorBoundary>
            }
          />
          <Route
            path="/test-dnd"
            element={
              <ErrorBoundary>
                <TestDnd />
              </ErrorBoundary>
            }
          />
        </Routes>
      </Router>
    </Provider>
  );
}

export default App;