import React, { useState } from 'react';
import { DndProvider, useDrag, useDrop } from 'react-dnd';
import { HTML5Backend } from 'react-dnd-html5-backend';
import { Box, Typography, Button, TextField } from '@mui/material';

const ItemTypes = {
  ELEMENT: 'element',
};

const DraggableElement = ({ id, content, type, left, top, onMove }) => {
  const [{ isDragging }, drag] = useDrag({
    type: ItemTypes.ELEMENT,
    item: { id, left, top },
    collect: (monitor) => ({
      isDragging: monitor.isDragging(),
    }),
  });

  return (
    <div
      ref={drag}
      style={{
        position: 'absolute',
        left,
        top,
        padding: '5px',
        background: type === 'text' ? '#e0e0e0' : 'transparent',
        border: '1px dashed #000',
        opacity: isDragging ? 0.5 : 1,
        cursor: 'move',
      }}
      onDragEnd={(e) => {
        const { clientX, clientY } = e;
        const canvas = document.getElementById('label-canvas');
        const rect = canvas.getBoundingClientRect();
        onMove(id, clientX - rect.left - 10, clientY - rect.top - 10);
      }}
    >
      {type === 'text' && content}
    </div>
  );
};

const LabelCanvas = ({ elements, onMove, onDrop }) => {
  const [, drop] = useDrop({
    accept: ItemTypes.ELEMENT,
    drop: (item, monitor) => {
      const delta = monitor.getDifferenceFromInitialOffset();
      const left = Math.round(item.left + delta.x);
      const top = Math.round(item.top + delta.y);
      onMove(item.id, left, top);
    },
  });

  return (
    <div
      ref={drop}
      id="label-canvas"
      style={{
        width: '400px',
        height: '300px',
        border: '2px solid #000',
        position: 'relative',
        background: '#fff',
      }}
    >
      {elements.map((element) => (
        <DraggableElement
          key={element.id}
          {...element}
          onMove={onMove}
        />
      ))}
    </div>
  );
};

const LabelDesigner = () => {
  const [elements, setElements] = useState([
    { id: 1, type: 'text', content: 'Product Name', left: 10, top: 10 },
  ]);
  const [newText, setNewText] = useState('');

  const handleMove = (id, left, top) => {
    setElements((prev) =>
      prev.map((element) =>
        element.id === id ? { ...element, left, top } : element
      )
    );
  };

  const handleAddText = () => {
    if (!newText) return;
    const newElement = {
      id: elements.length + 1,
      type: 'text',
      content: newText,
      left: 10,
      top: 10 + (elements.length * 30),
    };
    setElements((prev) => [...prev, newElement]);
    setNewText('');
  };

  const printLabel = async () => {
    const printContent = `
      <html>
        <body onload="window.print(); window.close();">
          <div style="width: 400px; height: 300px; border: 2px solid #000; position: relative;">
            ${elements.map(element => `
              <div style="
                position: absolute;
                left: ${element.left}px;
                top: ${element.top}px;
                padding: 5px;
                background: ${element.type === 'text' ? '#e0e0e0' : 'transparent'};
                border: 1px dashed #000;
              ">
                ${element.type === 'text' ? element.content : ''}
              </div>
            `).join('')}
          </div>
        </body>
      </html>
    `;
    if (window.electronAPI) {
      try {
        const result = await window.electronAPI.printReceipt(printContent);
        if (result.success) {
          alert(`Label PDF saved at: ${result.filePath}`);
        }
      } catch (err) {
        console.error('Printing failed:', err);
        alert('Failed to generate label PDF');
      }
    } else {
      const printWindow = window.open('', '_blank');
      printWindow.document.write(printContent);
      printWindow.document.close();
    }
  };

  return (
    <DndProvider backend={HTML5Backend}>
      <Box sx={{ padding: '20px' }}>
        <Typography variant="h6" gutterBottom>Label Designer</Typography>
        <Box sx={{ mb: 2 }}>
          <TextField
            label="Add Text Element"
            value={newText}
            onChange={(e) => setNewText(e.target.value)}
            sx={{ mr: 1 }}
          />
          <Button
            variant="contained"
            onClick={handleAddText}
            disabled={!newText}
          >
            Add Text
          </Button>
        </Box>
        <LabelCanvas elements={elements} onMove={handleMove} />
        <Button
          variant="contained"
          onClick={printLabel}
          sx={{ mt: 2 }}
        >
          Print Label
        </Button>
      </Box>
    </DndProvider>
  );
};

export default LabelDesigner;